                  <el-col :span='12' >
                    <area-select @areaGet='areaGetA' :areaName='areaName' :areaSet='areaNameA'/>
                  </el-col>
              </el-row>
            </el-col>
            <el-col :span='2' class="flexCenter borderSet fontSet">时间:</el-col>
            <el-col :span='10'>
              <el-row>
                <el-col :span='14'>
                  <div>
                      <el-radio-group v-model="timeA" >
                        <el-radio-button label="近7日"></el-radio-button>
                        <el-radio-button label="近30日"></el-radio-button>
                        <el-radio-button label="近1年"></el-radio-button>
                        <el-radio-button label="自定义时间"></el-radio-button>
                      </el-radio-group>
                    </div>
                </el-col>
                <el-col :span='9'>
                  <div class="block" style="width:100%;">
                    <el-date-picker
                        style="width:100%;"
                        :disabled='datePickerAbleA'
                        v-model="timeRangeA"
                        type="daterange"
                        :shortcuts="shortcuts"
                        range-separator="至"
                        start-placeholder="开始日期"
                        end-placeholder="结束日期"
                        value-format="yyyy-MM-dd"
                        align="right">
                    </el-date-picker>
                  </div>
                </el-col>
              </el-row>
            </el-col>
            <el-col :span='4'>
            </el-col>
          </el-row>
        <el-row class="marginTop10">
            <el-col :span='2' class="flexCenter fontSet">对比区域:</el-col>
            <el-col :span='10'>
              <el-row>
                  <el-col :span='12'>
                    <market @marketGet='marketGetB' :marketSet='marketNameB'/>
                  </el-col>
                  <el-col :span='12' >
                    <area-select @areaGet='areaGetB' :areaSet='areaNameB'/>
                  </el-col>
              </el-row>
            </el-col>
            <el-col :span='2' class="flexCenter borderSet fontSet">时间:</el-col>
            <el-col :span='10'>
              <el-row>
                <el-col :span='14'>
                   <div>
                      <el-radio-group v-model="timeB" >
                        <el-radio-button label="近7日"></el-radio-button>
                        <el-radio-button label="近30日"></el-radio-button>
                        <el-radio-button label="近1年"></el-radio-button>
                        <el-radio-button label="自定义时间"></el-radio-button>
                      </el-radio-group>
                    </div>
                </el-col>
                <el-col :span='9'>
                  <div class="block" style="width:100%;">
                    <el-date-picker
                        style="width:100%;"
                        :disabled='datePickerAbleB'
                        v-model="timeRangeB"
                        type="daterange"
                        :shortcuts="shortcuts"
                        range-separator="至"
                        start-placeholder="开始日期"
                        end-placeholder="结束日期"
                        align="right">
                    </el-date-picker>
                  </div>
                </el-col>
              </el-row>
            </el-col>
            <el-col :span='4'>
            </el-col>
          </el-row>
      </el-card>
      
      <!-- //下侧 -->
      <el-row >
          <el-col :span='8'>
              <el-card class="marginTop10" shadow='hover'>
                <el-row>
                  <el-col  class="flexCenter fontSet" :span='4'>指标：</el-col>
                <el-col :span='20'>
                    <el-radio-group v-model="indexName">
                            <el-radio-button label="客流量"></el-radio-button>
                            <el-radio-button label="集客力"></el-radio-button>
                            <el-radio-button label="提袋率"></el-radio-button>
                            <el-radio-button label="游逛深度"></el-radio-button>
                          </el-radio-group>
                </el-col>
                </el-row>
              </el-card>
              <el-card header="流量对比" style="font-weight: bold;margin-top:10px">
                <template #header>
                  <el-row>
                    <el-col :span='10' style="line-height: 40px;">
                      <span>流量对比</span>
                    </el-col>
                    <el-col :span='14'>
                      <el-col :span="12">
                      <span style="line-height: 40px;">间隔选择</span>
                      </el-col>
                      <el-col :span='10'  :offset='1'>
                        <el-select v-model="interval" clearable placeholder="请选择">
                          <el-option label="一天" value="一天"></el-option>
                          <el-option label="一周" value="一周"></el-option>
                          <el-option label="一月" value="一月"></el-option>
                        </el-select>
                      </el-col>
                      
                    </el-col>
                  </el-row>
                </template>
                <div ref="myEchart1" style="height: 390px;width:100%;" >
                  </div>
              </el-card>
          </el-col>
<!-- 参考区域 -->
        <el-col :span='8'> 
            <el-card class="marginTop10 marginLeft10" shadow="hover">
              <template #header>
                <div class="card-header">
                  <span>参考区域</span>
                </div>
              </template>
              <el-row>
                <el-col :span='12'>
                    <small-tag :options='tagA.all' class="marginLeft10"/>
                </el-col>
                <el-col :span='12'>
