                </el-col>
                <el-col :span='12'>
                    <small-tag :options='tagA.upDown' class="marginLeft10"/>
                </el-col>
              </el-row>
            </el-card>
        </el-col>
<!-- 对比区域 -->
        <el-col :span='8'> 
            <el-card class="marginTop10 marginLeft10" shadow="hover">
              <template #header>
                <div class="card-header">
                  <span>对比区域</span>
                </div>
              </template>
              <el-row>
                <el-col :span='12'>
                    <small-tag :options='tagB.all' class="marginLeft10"/>
                </el-col>
                <el-col :span='12'>
                    <small-tag :options='tagB.avgDay' class="marginLeft10"/>
                </el-col>
              </el-row>
              <el-row class="marginTop10">
                <el-col :span='12'>
                    <small-tag :options='tagB.avgWeek' class="marginLeft10"/>
                </el-col>
                <el-col :span='12'>
                    <small-tag :options='tagB.avgMonth' class="marginLeft10"/>
                </el-col>
              </el-row>
              <el-row class="marginTop10">
                <el-col :span='12'>
                    <small-tag :options='tagB.week' class="marginLeft10"/>
                </el-col>
                <el-col :span='12'>
                    <small-tag :options='tagB.upDown' class="marginLeft10"/>
                </el-col>
              </el-row>
            </el-card>
        </el-col>
      </el-row>
  </div>
</template>
<script>
import Crumbs from '../pages/Crumbs'
import AreaSelect from '../pages/indexSelect/AreaSelect'
import MarketSelect from '../pages/indexSelect/MarketSelect'
import smallTag from '../pages/smallTag'
import axios from 'axios'
import * as echarts from 'echarts'
import ExportExcel from '../pages/excel/ExportExcel'

export default {
  data(){
    return {
        dataAll:[],
        areaNameA:'全部',//选择的区域
        timeA:'近1年',//选择的时间
        areaNameB:'全部',//选择的区域
        timeB:'近1年',//选择的时间
        timeRangeA: '',//选择的时间范围
        timeRangeB: '',//选择的时间范围
        marketNameA:'万达',//选择的商场名称
        marketNameB:'万达',//选择的商场名称
        indexName:'客流量',//选择的指标
        shortcuts: [{
                text: '最近一周',
                value: (() => {
                  const end = new Date();
                  const start = new Date();
                  start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
                  return [this.$moment().subtract(7, 'days'), end]
                })()
              }, {
                text: '最近一月',
                value: (() => {
                  const end = new Date();
                  const start = new Date();
                  start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
                  return [this.$moment().subtract(1, 'months'), end]
                })()
              }, {
                text: '最近一年',
                value: (() => {
                  const end = new Date();
                  const start = new Date();
                  start.setFullYear(start.getFullYear()-1)
                  // start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
                  return [this.$moment().subtract(1, 'years'), end]
                })()
              }],
            datePickerAbleA:true,
            datePickerAbleB:true,
            tagA:{
              all:{
                icon:{
                  width:'20%',
                  bgcolor:'#67C23A'
                },
                content:{
                  title:{
                    title:'总客流量'
                  },
                  value:{
                    value:'#E6A23C'
                  }
                }
              },
              avgDay:{
                icon:{
                  width:'20%',
                  bgcolor:'#e89a3c'
                },
                content:{
                  title:{
                    title:'集团占比'
                  },
                  value:{
                    value:''
                  }
                }
              },
              avgWeek:{
                icon:{
                  width:'20%',
                  bgcolor:'#a9d134'
                },
                content:{
                  title:{
                    title:'当月占比'
                  },
                  value:{
                    value:''
                  }
                }
              },
              avgMonth:{
                icon:{
                  width:'20%',
                  bgcolor:'#d8bd14'
                },
                content:{
                  title:{
                    title:'集客力'
                  },
                  value:{
                    value:''
                  }
                }
              },
              week:{
                icon:{
                  width:'20%',
                  bgcolor:'#33bcb7'
                },
                content:{
                  title:{
                    title:'日均客流量'
                  },
                  value:{
                    value:''
                  }
                }
              },
              upDown:{
                icon:{
                  width:'20%',
                  bgcolor:'#49aa19'
                },
                content:{
                  title:{
                    title:'游逛深度'
                  },
                  value:{
                    value:''
                  }
                }
              },
              textWidth:''
            },
            tagB:{
              all:{
                icon:{
                  width:'20%',
                  bgcolor:'#67C23A'
                },
                content:{
                  title:{
                    title:'总客流量'
                  },
                  value:{
                    value:'#E6A23C'
                  }
                }
              },
              avgDay:{
                icon:{
                  width:'20%',
                  bgcolor:'#e89a3c'
                },
                content:{
                  title:{
                    title:'集团占比'
                  },
                  value:{
                    value:''
                  }
                }
              },
              avgWeek:{
                icon:{
                  width:'20%',
                  bgcolor:'#a9d134'
                },
                content:{
                  title:{
                    title:'当月占比'
                  },
                  value:{
                    value:''
                  }
                }
              },
              avgMonth:{
                icon:{
                  width:'20%',
                  bgcolor:'#d8bd14'
                },
                content:{
                  title:{
                    title:'集客力'
                  },
                  value:{
                    value:''
                  }
                }
              },
              week:{
                icon:{
                  width:'20%',
                  bgcolor:'#33bcb7'
                },
                content:{
                  title:{
                    title:'日均客流量'
                  },
                  value:{
                    value:''
                  }
                }
              },
              upDown:{
                icon:{
                  width:'20%',
                  bgcolor:'#49aa19'
                },
                content:{
                  title:{
                    title:'游逛深度'
                  },
                  value:{
                    value:''
                  }
                }
              },
              textWidth:''
            },
        chart1:{},
        chartData1:[],
        interval:'一月',
        screenWidth: document.body.clientWidth,   


    }
  },
  components:{
    crumbs:Crumbs,
    areaSelect:AreaSelect,
    market:MarketSelect,
    smallTag,
    ExportExcel

    // indexSelect:IndexSelect,
  },
  methods:{
    exportData(){
      axios.all([axios(`https://mock.mengxuegu.com/mock/6076fee5990ff82a18f95b5f/item/getFlowComparison?lg=${this.timeCalc(this.timeRangeA[0],this.timeRangeA[1]).length}`),axios(`https://mock.mengxuegu.com/mock/6076fee5990ff82a18f95b5f/item/getFlowComparison?lg=${this.timeCalc(this.timeRangeB[0],this.timeRangeB[1]).length}`)]).then(
        axios.spread((...res)=>{
          this.dataAll=[]
          let data0=res[0].data.list
          if(this.areaNameA!='全部'){
            data0=data0.filter(item=>item.area==this.areaNameA)
          }
          let allDate0=this.timeCalc(this.timeRangeA[0],this.timeRangeA[1])
          let arrAll=[0,0,0,0]
          for(let i=0;i<allDate0.length;i++){
              arrAll[0]+=data0.flow[i]
              arrAll[1]+=data0.getPower[i]
              arrAll[2]+=data0.rate[i]
              arrAll[3]+=data0.deep[i]
          }
          let arr=[0,0,0,0]
          for(let i=0;i<allDate0.length;i++){
            if(this.$moment(allDate0[i]).isoWeekday()==6||this.$moment(allDate0[i]).isoWeekday()==7){
              arr[0]+=data0.flow[i]
              arr[1]+=data0.getPower[i]
              arr[2]+=data0.rate[i]
              arr[3]+=data0.deep[i]
            }
          }
          this.dataAll.push({
            date:'全部',
            flow:arrAll[0],
            getPower:arrAll[1],
            rate:arrAll[2],
            deep:arrAll[3],
            marketName:this.marketNameA,
            areaName:data0.area
          })
          this.dataAll.push({
            date:'周末',
            flow:arr[0],
            getPower:arr[1],
            rate:arr[2],
            deep:arr[3],
            marketName:this.marketNameA,
            areaName:data0.area
          })
          this.dataAll.push({
            date:'月平均',
            flow:(arrAll[0]/30).toFixed(2),
            getPower:(arrAll[1]/30).toFixed(2),
            rate:(arrAll[2]/30).toFixed(2),
            deep:(arrAll[3]/30).toFixed(2),
            marketName:this.marketNameA,
            areaName:data0.area
          })
          this.dataAll.push({
            date:'周平均',
            flow:(arrAll[0]/7).toFixed(2),
            getPower:(arrAll[1]/7).toFixed(2),
            rate:(arrAll[2]/7).toFixed(2),
            deep:(arrAll[3]/7).toFixed(2),
            marketName:this.marketNameA,
            areaName:data0.area
          })
          this.dataAll.push({
            date:'天平均',
            flow:(arrAll[0]/allDate0.length).toFixed(2),
            getPower:(arrAll[1]/allDate0.length).toFixed(2),
            rate:(arrAll[2]/allDate0.length).toFixed(2),
            deep:(arrAll[3]/allDate0.length).toFixed(2),
            marketName:this.marketNameA,
            areaName:data0.area
          })
          for(let i=0;i<allDate0.length;i++){
            this.dataAll.push({
            date:allDate0[i],
            flow:data0.flow[i],
            getPower:data0.getPower[i],
            rate:data0.rate[i],
            deep:data0.deep[i],
            marketName:this.marketNameA,
            areaName:data0.area
          })
          }
          let data1=res[1].data.list
          if(this.areaNameB!='全部'){
            data1=data1.filter(item=>item.area==this.areaNameB)
          }
          let allDate1=this.timeCalc(this.timeRangeB[0],this.timeRangeB[1])
          arrAll=[0,0,0,0]
          for(let i=0;i<allDate1.length;i++){
              arrAll[0]+=data1.flow[i]
              arrAll[1]+=data1.getPower[i]
              arrAll[2]+=data1.rate[i]
              arrAll[3]+=data1.deep[i]
          }
          arr=[0,0,0,0]
          for(let i=0;i<allDate1.length;i++){
            if(this.$moment(allDate1[i]).isoWeekday()==6||this.$moment(allDate1[i]).isoWeekday()==7){
              arr[0]+=data1.flow[i]
              arr[1]+=data1.getPower[i]
              arr[2]+=data1.rate[i]
              arr[3]+=data1.deep[i]
            }
          }
          this.dataAll.push({
            date:'全部',
            flow:arrAll[0],
            getPower:arrAll[1],
            rate:arrAll[2],
            deep:arrAll[3],
            marketName:this.marketNameB,
            areaName:data1.area
          })
          this.dataAll.push({
            date:'周末',
            flow:arr[0],
            getPower:arr[1],
            rate:arr[2],
            deep:arr[3],
            marketName:this.marketNameB,
            areaName:data1.area
          })
          this.dataAll.push({
            date:'月平均',
            flow:(arrAll[0]/30).toFixed(2),
            getPower:(arrAll[1]/30).toFixed(2),
            rate:(arrAll[2]/30).toFixed(2),
            deep:(arrAll[3]/30).toFixed(2),
            marketName:this.marketNameB,
            areaName:data1.area
          })
          this.dataAll.push({
            date:'周平均',
            flow:(arrAll[0]/7).toFixed(2),
            getPower:(arrAll[1]/7).toFixed(2),
            rate:(arrAll[2]/7).toFixed(2),
            deep:(arrAll[3]/7).toFixed(2),
            marketName:this.marketNameB,
            areaName:data1.area
          })
          this.dataAll.push({
            date:'天平均',
            flow:(arrAll[0]/allDate1.length).toFixed(2),
            getPower:(arrAll[1]/allDate1.length).toFixed(2),
            rate:(arrAll[2]/allDate1.length).toFixed(2),
            deep:(arrAll[3]/allDate1.length).toFixed(2),
            marketName:this.marketNameB,
            areaName:data1.area
          })
          for(let i=0;i<allDate1.length;i++){
            this.dataAll.push({
            date:allDate1[i],
            flow:data1.flow[i],
            getPower:data1.getPower[i],
            rate:data1.rate[i],
            deep:data1.deep[i],
            marketName:this.marketNameB,
            areaName:data1.area
          })
          }
        })
      )
    },
    marketGetA(val){
      this.marketNameA=val
      },
    areaGetA(val){
        this.areaNameA=val
      },
    timeGetA(val){
        console.log(val)
        this.timeA=val
        if(val=='自定义时间'){
          this.datePickerAbleA=false
        }else this.datePickerAbleA=true
    },
    timeIntval(startTime,endTime){
          let arr = []
          let moment=this.$moment
          startTime = moment(startTime).format('YYYY-MM-DD')
          endTime = moment(endTime).format('YYYY-MM-DD')
          while (moment(startTime).isBefore(endTime)) {
            arr.push(moment(startTime).format('YYYY-MM-DD'))
            startTime = moment(startTime).add(1, 'days').format('YYYY-MM-DD')
          }
          arr.push(moment(endTime).format('YYYY-MM-DD'))
          return arr
      },
    timeCalc(startTime,endTime){
          let arr = []
          let moment=this.$moment
          startTime = moment(startTime).format('YYYY-MM-DD')
          endTime = moment(endTime).format('YYYY-MM-DD')
          while (moment(startTime).isBefore(endTime)) {
            arr.push(moment(startTime).format('YYYY-MM-DD'))
            if(this.interval=='一天'){
            startTime = moment(startTime).add(1, 'days').format('YYYY-MM-DD')
            }else if(this.interval=='一周'){
            startTime = moment(startTime).add(7, 'days').format('YYYY-MM-DD')
            }else {
            startTime = moment(startTime).add(1, 'months').format('YYYY-MM-DD')
            }
          }
          arr.push(moment(endTime).format('YYYY-MM-DD'))
          return arr
    },
    marketGetB(val){
      this.marketNameB=val
      },
    areaGetB(val){
        this.areaNameB=val
      },
    timeGetB(val){
        console.log(val)
        this.timeB=val
        if(val=='自定义时间'){
          this.datePickerAbleB=false
        }else this.datePickerAbleB=true
    },
    onQuery(){
      this.tagChangeA()
      this.tagChangeB()
      this.dataChangeA()
      this.dataChangeB()
    }
    ,
    reSet(){
      this.marketNameA='万达'
      this.marketNameB='万达'
      this.areaNameA='全部'
      this.areaNameB='全部'
      this.timeA='近1年'
      this.timeB='近1年'
      this.indexName='客流量'
    },
    tagChangeA(bool=true){
      if(bool&&this.marketNameA==this.marketNameB&&this.areaNameA==this.areaNameB&&this.$moment(this.timeRangeA[0]).format('YYYY-MM-DD')==this.$moment(this.timeRangeB[0]).format('YYYY-MM-DD')&&this.$moment(this.timeRangeA[1]).format('YYYY-MM-DD')==this.$moment(this.timeRangeB[1]).format('YYYY-MM-DD')){
            this.tagA.all.content.value.value=this.tagB.all.content.value.value
            this.tagA.avgDay.content.value.value=this.tagB.avgDay.content.value.value
            this.tagA.avgWeek.content.value.value=this.tagB.avgWeek.content.value.value
            this.tagA.avgMonth.content.value.value=this.tagB.avgMonth.content.value.value
            this.tagA.week.content.value.value=this.tagB.week.content.value.value
            this.tagA.upDown.content.value.value='0%'
            this.tagA.all.content.title.title='总'+this.indexName
            this.tagA.avgDay.content.title.title='日平均'+this.indexName
            this.tagA.avgWeek.content.title.title='周平均'+this.indexName
            this.tagA.avgMonth.content.title.title='月平均'+this.indexName
            this.tagA.week.content.title.title='周末'+this.indexName
            this.tagA.upDown.content.title.title=this.indexName+'涨跌比'
            this.tagB.upDown.content.value.value='0%'
      }else{
        axios(`https://mock.mengxuegu.com/mock/6076fee5990ff82a18f95b5f/item/getFlowComparison?lg=${this.timeIntval(this.timeRangeA[0],this.timeRangeA[1]).length}`).then(res=>{
