      }
      if(val==="近30日"){
        this.timeRangeA=[this.$moment().subtract(1, 'months'),this.$moment()]
      }
      if(val==="近1年"){
        this.timeRangeA=[this.$moment().subtract(1, 'year'),this.$moment()]
      }
      if(val=='自定义时间'){
          this.datePickerAbleA=false
        }else this.datePickerAbleA=true
